.product {
    &__list {
        padding-top: 10px;
        max-width: 800px;
        width: 100%;
        @include media($lg) {
            max-width: 700px;
        }
        @include media($md) {
            max-width: 500px;
        }
        @include media($sm) {
            max-width: 400px;
        }
        @include media($sm) {
            max-width: 300px;
            margin: 0 auto;
        }

        .slider-for {
            max-width: 800px;
            width: 100%;
            margin: 0 auto;
            position: relative;
            z-index: 10;
            @include media($lg) {
                max-width: 700px;
            }
            @include media($md) {
                max-width: 500px;
            }
            @include media($sm) {
                max-width: 400px;
            }
            @include media($xs) {
                max-width: 300px;
            }
        }

        .slide-container {
            height: 60vh;
            // background-color: #fff;
            // text-align: center;
            // line-height: 50vh;
            // font-size: 50px;
            // font-weight: bold;
            border: 3px solid $green;
            margin-bottom: 20px;
            border-radius: 5px;
            &:focus {
                outline: none;
            }

            @include media($sm) {
                height: 40vh;
            }

            img {
                height: 100%;
                width: 100%;
                // object-fit: cover;
                position: relative;
            }

            .slide-desc {
                position: absolute;
                top: 20px;
                left: 20px;
                background: $darkGreen;
                width: 500px;
                padding: 10px;
                border: 5px solid $orange;
                border-radius: 10px;
                transition: .3s;
                opacity: 1;
                @include media($md) {
                    width: 300px;
                    top: 10px;
                }
                @include media($sm) {
                    width: 200px;
                    top: 5px;
                    left: 5px;
                    border: 2px solid $orange;
                }
                h5 {
                    text-align: center;
                    font-size: 24px;
                    color: $white;
                    margin-bottom: 15px;
                    @include media($md) {
                        font-size: 18px;
                    }
                    @include media($sm) {
                        font-size: 15px;
                    }
                }

                p {
                    font-size: 20px;
                    color: $white;
                    @include media($md) {
                        font-size: 16px;
                    }
                    @include media($sm) {
                        font-size: 12px;
                    }
                }
            }

            &:hover {
                .slide-desc {
                    opacity: 0;
                }
            }
        }

        .slide-btn {
            // text-align: center;
            // box-sizing: border-box;
            // padding: 50px 30px;
            // background-color: yellow;
            border: 3px solid $green;
            border-radius: 5px;
            width: 150px;
            height: 150px;
            cursor: pointer;
            img {
                height: 100%;
                width: 100%;
                // object-fit: cover;
                position: relative;
            }
            &:focus {
                border: 3px solid $orange;
                outline: $orange;
            }
        }
    }
}