.managers {
    &__content {
        display: flex;
        flex-direction: column;
    }

    &__person {
        display: flex;
        margin: 20px 0;
        background: $grey;

        @include media($sm) {
            flex-direction: column;
            align-items: center;
            text-align: center;
        }
        img {
            display: block;
            @include media($sm) {
                max-width: 150px;
                width: 100%;
                max-height: 150px;
                height: 100%;
                padding-top: 10px;
            }
        }

        &-info {
            padding: 20px;

            h1 {
                font-size: 24px;
                font-weight: 700;
                @include media($sm) {
                    font-size: 20px;
                }
            }

            h2 {
                font-size: 18px;
                padding: 10px 0;
                @include media($sm) {
                    font-size: 16px;
                }
            }

            .managers__text {
                font-size: 16px;
                @include media($sm) {
                    font-size: 14px;
                }
                p {
                    padding: 15px;
                }
            }
        }
    }
}