* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

html {
    height: 100%;
}

body {
    font-family: "Roboto";
    background-image: url(../images/bg.jpg);
    height: 100%;
}

main {
    min-height: 100%;
    padding-bottom: 30px;
}



footer {
    width: 100%;
}

.container {
    @include container();
}

.row {
    @include row();
}

::after,
::before {
    font-family: "fontawesome";
}

.icon {
    &-download::before {
        content: "\f56d";
        font-weight: 300;
        padding-right: 10px;
        font-size: 24px;
        vertical-align: text-bottom;

        @include media($md) {
            font-size: 16px;
        }
    }

    &-search::before {
        content: "\f002";
        font-weight: 300;
    }

    &-down::before {
        content: "\f078";
        font-weight: 300;
    }

    &-circle::before {
        content: "\f111";
        font-weight: 300;
        margin-right: 10px;
        font-size: 8px;
        vertical-align: middle;
    }

    &-right::before {
        content: "\f105";
        font-weight: 300;
        font-size: 22px;
    }

    &-left::before {
        content: "\f104";
        font-weight: 300;
        font-size: 22px;
    }

    &-burger::before {
        content: "\f0c9";
        font-weight: 300;
    }

    &-sliderleft::before {
        content: "\f053";
        font-weight: 300;
    }

    &-sliderright::before {
        content: "\f054";
        font-weight: 300;
    }

    &-hand::before {
        content: "\f2b5";
        font-weight: 700;
    }

    &-metall::before {
        content: "\f856";
        font-weight: 300;
    }

    &-eco::before {
        content: "\f06c";
        font-weight: 700;
    }

    &-sync::before {
        content: "\f2f1";
        font-weight: 700;
    }

    &-pay::before {
        content: "\f653";
        font-weight: 700;
    }

    &-truck::before {
        content: "\f0d1";
        font-weight: 700;
    }

    &-factory::before {
        content: "\f3b3";
        font-weight: 300;
    }

    &-home::before {
        content: "\f80b";
        font-weight: 700;
    }

    &-news::before {
        content: "\f1ea";
        font-weight: 300;
        padding-right: 10px;
        font-size: 24px;
        vertical-align: middle;

        @include media($lg) {
            font-size: 14px;
        }
    }

    &-plus::before {
        content: "\f055";
        font-weight: 300;
    }

    &-close::before {
        content: "\f00d";
        font-weight: 700;
    }

    &-location::before {
        content: "\f606";
        font-weight: 700;
        margin-right: 5px;
    }

    &-phone::before {
        content: "\f095";
        font-weight: 700;
        margin-right: 5px;
    }

    &-letter::before {
        content: "\f0e0";
        font-weight: 700;
        margin-right: 5px;
    }

    &-case::before {
        content: "\f0b1";
        font-weight: 700;
        margin-right: 5px;
    }
}

.contentPages {
    display: grid;
    grid-template-areas: "content content content aside";
    grid-template-columns: 1fr;
    padding-top: 10px;
    gap: 10px;

    @include media($xs) {
        grid-template-areas: "content"
            "aside";
        grid-template-columns: 1fr;
    }
}

.aside {
    grid-area: "aside";
    max-width: 250px;
    width: 100%;
    display: flex;
    flex-direction: column;

    @include media($xs) {
        max-width: 100%;
        align-items: center;
        text-align: center;
    }

    &__nav {
        margin-bottom: 30px;
        width: 100%;
    }

    &__title {
        font-size: 20px;
        text-transform: uppercase;
        margin-bottom: 15px;
        padding: 5px 0;
        position: relative;
        width: max-content;

        @include media($sm) {
            font-size: 16px;
        }

        @include media($xs) {
            margin: 0 auto 15px auto;
        }

        &::after {
            content: '';
            position: absolute;
            width: 100%;
            height: 3px;
            background: $orange;
            bottom: 0;
            left: 0;
        }
    }

    &__menu {
        background: $grey;
    }


    &__link {
        display: block;
        color: $black;
        font-size: 16px;
        text-transform: uppercase;
        padding: 15px 10px;
        transition: .5s;

        @include media($sm) {
            font-size: 12px;
            padding: 10px 5px;
        }


        &:hover {
            background: $bgGreen;
            color: $white;
        }

        &.active {
            background: $bgGreen;
            color: $white;
        }
    }

    &__contacts {
        display: flex;
        flex-direction: column;

        p {
            font-family: "Play";
            font-size: 16px;
            padding: 5px 0;
            color: $black;

            &::before {
                color: $orange;
            }

            @include media($sm) {
                font-size: 12px;
            }
        }
    }
}

.content__title {
    font-size: 24px;
    text-transform: uppercase;
    width: max-content;
    position: relative;
    padding: 5px 0;
    margin-bottom: 15px;

    @include media($xs) {
        font-size: 14px;
        margin: 0 auto;
    }

    &::after {
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: 3px;
        background: $orange;
        left: 0;
        bottom: 0;
    }
}

.slick-arrow {
    width: 80px;
    height: 80px;
    color: $white;
    background: $darkGreen;
    z-index: 999;
    outline: none;
    @include media($sm) {
        width: 60px;
        height: 60px;
    }
    
    &::before {
        font-size: 36px;
        color: $white;
        @include media($sm) {
            font-size: 24px;
        }
    }

    &:hover {
        background: $green;
    }

    &:focus {
        outline: none;
        background: $green;
    }

}

.slick-prev {
    left: 0;
    top: auto;
    bottom: -40px;
    @include media($sm) {
        top: 50%;
    }
}

.slick-next {
    left: 80px;
    top: auto;
    bottom: -40px;
    @include media($sm) {
        top: 50%;
        left: auto;
        right: 0;
    }
}