.contentPages {
  display: grid;
  grid-template-areas: "content content content aside";
  grid-template-columns: 1fr;
  padding-top: 10px;
  gap: 10px;
}

@media (max-width: 576px) {
  .contentPages {
    grid-template-areas: "content" "aside";
    grid-template-columns: 1fr;
  }
}

.aside {
  grid-area: "aside";
  max-width: 250px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

@media (max-width: 576px) {
  .aside {
    max-width: 100%;
    align-items: center;
    text-align: center;
  }
}

.aside__nav {
  margin-bottom: 30px;
  width: 100%;
}

.aside__title {
  font-size: 20px;
  text-transform: uppercase;
  margin-bottom: 15px;
  padding: 5px 0;
  position: relative;
  width: max-content;
}

@media (max-width: 768px) {
  .aside__title {
    font-size: 16px;
  }
}

@media (max-width: 576px) {
  .aside__title {
    margin: 0 auto 15px auto;
  }
}

.aside__title::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 3px;
  background: #eba119;
  bottom: 0;
  left: 0;
}

.aside__menu {
  background: #e9e7e7;
}

.aside__link {
  display: block;
  color: #000;
  font-size: 16px;
  text-transform: uppercase;
  padding: 15px 10px;
  transition: .5s;
}

@media (max-width: 768px) {
  .aside__link {
    font-size: 12px;
    padding: 10px 5px;
  }
}

.aside__link:hover {
  background: #24c623;
  color: #fff;
}

.aside__link.active {
  background: #24c623;
  color: #fff;
}

.aside__contacts {
  display: flex;
  flex-direction: column;
}

.aside__contacts p {
  font-family: "Play";
  font-size: 16px;
  padding: 5px 0;
  color: #000;
}

.aside__contacts p::before {
  color: #eba119;
}

@media (max-width: 768px) {
  .aside__contacts p {
    font-size: 12px;
  }
}

.content__title {
  font-size: 24px;
  text-transform: uppercase;
  width: max-content;
  position: relative;
  padding: 5px 0;
  margin-bottom: 15px;
}

@media (max-width: 576px) {
  .content__title {
    font-size: 14px;
    margin: 0 auto;
  }
}

.content__title::after {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 3px;
  background: #eba119;
  left: 0;
  bottom: 0;
}

.slick-arrow {
  width: 80px;
  height: 80px;
  color: #fff;
  background: rgba(5, 194, 5, 0.6);
  z-index: 999;
  outline: none;
}

@media (max-width: 768px) {
  .slick-arrow {
    width: 60px;
    height: 60px;
  }
}

.slick-arrow::before {
  font-size: 36px;
  color: #fff;
}

@media (max-width: 768px) {
  .slick-arrow::before {
    font-size: 24px;
  }
}

.slick-arrow:hover {
  background: #11d111;
}

.slick-arrow:focus {
  outline: none;
  background: #11d111;
}

.slick-prev {
  left: 0;
  top: auto;
  bottom: -40px;
}

@media (max-width: 768px) {
  .slick-prev {
    top: 50%;
  }
}

.slick-next {
  left: 80px;
  top: auto;
  bottom: -40px;
}

@media (max-width: 768px) {
  .slick-next {
    top: 50%;
    left: auto;
    right: 0;
  }
}

.nav__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 0;
}

@media (max-width: 576px) {
  .nav__info {
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }
}

.nav__logo {
  display: flex;
  align-items: center;
}

.nav__logo div {
  max-width: 250px;
  width: 100%;
  margin-left: 10px;
}

.nav__logo div h1 {
  font-family: "Orbitron";
  font-family: 700px;
  text-transform: uppercase;
}

.nav__logo div p {
  font-family: "Play";
  font-family: 400;
  text-transform: uppercase;
  font-size: 14px;
}

@media (max-width: 576px) {
  .nav__catalog {
    margin-top: 30px;
    display: inline-block;
  }
}

.nav__catalog a {
  font-family: "Play";
  font-size: 20px;
  text-transform: uppercase;
  color: #fff;
  transition: .5s;
}

.nav__catalog a:hover {
  background: linear-gradient(to bottom, #ebb41e, #deb81c, #d1bc19, #c3bf17, #b6c314, #a9c712, #94c812, #7fc911, #6bca11, #56ca11, #41cb10, #2ccc10);
}

@media (max-width: 768px) {
  .nav__catalog a {
    font-size: 16px;
  }
}

.nav__catalog-pdf {
  background: linear-gradient(to bottom, #2fd923, #2bd120, #27c91d, #23c01b, #1fb818, #1bb015, #18a013, #169111, #13810f, #10710c, #0e620a, #0b5208);
  padding: 25px 40px 25px 45px;
}

@media (max-width: 768px) {
  .nav__catalog-pdf {
    padding: 20px 30px 20px 35px;
  }
}

@media (max-width: 576px) {
  .nav__catalog-pdf {
    padding: 10px 30px 10px 35px;
  }
}

.nav__catalog-doc {
  padding: 25px 20px 25px 20px;
  background: linear-gradient(to bottom, #2ceb1e, #28e41c, #24dd19, #20d517, #1cce14, #18c712, #17c011, #16b910, #15b30f, #13ac0e, #12a50d, #119e0c);
}

@media (max-width: 768px) {
  .nav__catalog-doc {
    padding: 20px 20px 20px 20px;
  }
}

@media (max-width: 576px) {
  .nav__catalog-doc {
    padding: 10px 20px 10px 20px;
  }
}

.nav__bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(to bottom, #2fd923, #2bd120, #27c91d, #23c01b, #1fb818, #1bb015, #18a013, #169111, #13810f, #10710c, #0e620a, #0b5208);
  position: relative;
  box-shadow: 0px 5px 23px -5px #000000;
}

@media (max-width: 992px) {
  .nav__bar {
    justify-content: center;
  }
}

@media (max-width: 768px) {
  .nav__bar {
    justify-content: space-between;
  }
}

.nav__menu {
  position: relative;
}

.nav__menu .burger {
  height: 25px;
  width: 30px;
  cursor: pointer;
  margin-left: 10px;
  position: relative;
  background: none;
  outline: none;
  border: none;
  display: none;
  z-index: 100;
}

@media (max-width: 768px) {
  .nav__menu .burger {
    display: block;
  }
}

.nav__menu .burger__line {
  display: block;
  width: 100%;
  height: 3px;
  background: #fff;
  position: absolute;
  left: 0;
  transition: .3s all linear;
  z-index: 0;
}

.nav__menu .burger__line_first {
  top: 0;
}

.nav__menu .burger__line_second, .nav__menu .burger__line_third {
  top: 50%;
  transform: translateY(-50%);
}

.nav__menu .burger__line_fourth {
  bottom: 0;
}

.nav__menu .burger_active .burger__line_first,
.nav__menu .burger_active .burger__line_fourth {
  opacity: 0;
}

.nav__menu .burger_active .burger__line_second {
  transform: translateY(-50%) rotate(45deg);
}

.nav__menu .burger_active .burger__line_third {
  transform: translateY(-50%) rotate(-45deg);
}

.nav__menu .menu {
  display: flex;
  z-index: 1000;
}

@media (max-width: 768px) {
  .nav__menu .menu {
    flex-direction: column;
    position: absolute;
    background: #24c623;
    top: 36px;
    border-top: 2px solid #eba119;
    transform: scaleY(0);
    transform-origin: 50% 0 0;
    transition: .5s;
    opacity: 0;
    min-width: 250px;
    width: 100%;
  }
  .nav__menu .menu.active {
    transform: scaleY(1);
    opacity: 1;
  }
}

@media (max-width: 576px) {
  .nav__menu .menu {
    top: 31px;
  }
}

.nav__menu .menu li.menu__item {
  position: relative;
  transition: 0.5s;
  /* .sub.active {
                    transform: scaleY(1);
                    opacity: 1;
                    @include media($sm) {
                        height: 100%;
                    }
                } */
}

.nav__menu .menu li.menu__item a {
  display: inline-block;
  padding: 20px;
  color: #fff;
  transition: .5s;
}

@media (max-width: 992px) {
  .nav__menu .menu li.menu__item a {
    padding: 15px;
  }
}

@media (max-width: 768px) {
  .nav__menu .menu li.menu__item a {
    width: 100%;
  }
}

.nav__menu .menu li.menu__item a:hover {
  background: #fcfcfc7a;
  color: #000;
}

.nav__menu .menu li.menu__item .sub {
  position: absolute;
  background: #1edd47;
  min-width: 250px;
  width: 100%;
  border-top: 2px solid #eba119;
  display: none;
  z-index: 100;
}

@media (max-width: 768px) {
  .nav__menu .menu li.menu__item .sub {
    position: unset;
  }
}

.nav__menu .menu li.menu__item .sub li a {
  width: 100%;
  z-index: 9999;
  transition: .5s;
  transform: translateX(0);
}

@media (max-width: 768px) {
  .nav__menu .menu li.menu__item .sub li a {
    padding-left: 30px;
    position: relative;
  }
  .nav__menu .menu li.menu__item .sub li a::before {
    content: "\f324";
    font-weight: 300;
    padding-right: 5px;
    color: #eba119;
  }
}

.nav__menu .menu li.menu__item:hover .sub {
  transform: scaleY(1);
  display: block;
  opacity: 1;
}

@media (max-width: 768px) {
  .nav__menu .menu li.menu__item:hover .sub {
    height: 100%;
  }
}

.nav__menu .menu li:nth-of-type(2) > .menu__link::after {
  content: "\f078";
  font-weight: 300;
  margin-left: 5px;
}

.nav__menu .menu li:nth-of-type(3) > .menu__link::after {
  content: "\f078";
  font-weight: 300;
  margin-left: 5px;
}

.nav__menu .menu li:nth-of-type(5) > .menu__link::after {
  content: "\f078";
  font-weight: 300;
  margin-left: 5px;
}

@media (max-width: 992px) {
  .nav__search {
    display: none;
  }
}

@media (max-width: 768px) {
  .nav__search {
    display: block;
  }
}

.nav__search input {
  padding: 21px 10px;
  border: none;
  outline: none;
  font-size: 15px;
  background: #fcfcfc7a;
  max-width: 150px;
  width: 100%;
}

@media (max-width: 768px) {
  .nav__search input {
    padding: 15px 8px;
  }
}

@media (max-width: 576px) {
  .nav__search input {
    max-width: 100px;
    padding: 10px 8px;
  }
}

.nav__search button {
  padding: 21px 15px;
  border: none;
  outline: none;
  font-size: 15px;
  background: #11d111;
  color: #fff;
  transition: .3s;
}

@media (max-width: 768px) {
  .nav__search button {
    padding: 15px 15px;
  }
}

@media (max-width: 576px) {
  .nav__search button {
    padding: 10px;
  }
}

.nav__search button:hover {
  background: #eba119;
}

.nav__secondary {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 768px) {
  .nav__secondary {
    justify-content: center;
  }
}

.nav__secondary ul {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.nav__secondary ul li a {
  padding: 20px;
  display: inline-block;
  font-size: 12px;
  color: grey;
  transition: .5s;
  position: relative;
  z-index: 1;
}

.nav__secondary ul li a::before {
  content: '';
  display: block;
  width: 5px;
  height: 5px;
  background: grey;
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: .5s;
  z-index: 1;
}

.nav__secondary ul li a:hover {
  color: #eba119;
}

.nav__secondary ul li a:hover::before {
  background: #eba119;
}

@media (max-width: 576px) {
  .nav__secondary ul li a {
    padding: 10px;
  }
  .nav__secondary ul li a::before {
    left: 0;
  }
}

.nav__secondary .nav__search {
  display: none;
}

@media (max-width: 992px) {
  .nav__secondary .nav__search {
    display: block;
  }
  .nav__secondary .nav__search input {
    border: 1px solid #9e9595;
    padding: 10px;
    border-radius: 10px;
  }
  .nav__secondary .nav__search button {
    padding: 10px;
    background: none;
    color: #9e9595;
    transition: 1s;
  }
  .nav__secondary .nav__search button:hover {
    color: #eba119;
    transform: rotateY(360deg);
  }
}

@media (max-width: 768px) {
  .nav__secondary .nav__search {
    display: none;
  }
}

