.product__list .slide-container .slide-desc {
  position: absolute;
  top: 20px;
  left: 20px;
  background: rgba(5, 194, 5, 0.6);
  width: 500px;
  padding: 10px;
  border: 5px solid #eba119;
  border-radius: 10px;
  transition: .3s;
  opacity: 1;
}

@media (max-width: 992px) {
  .product__list .slide-container .slide-desc {
    width: 300px;
    top: 10px;
  }
}

@media (max-width: 768px) {
  .product__list .slide-container .slide-desc {
    width: 200px;
    top: 5px;
    left: 5px;
    border: 2px solid #eba119;
  }
}

.product__list .slide-container .slide-desc h5 {
  text-align: center;
  font-size: 24px;
  color: #fff;
  margin-bottom: 15px;
}

@media (max-width: 992px) {
  .product__list .slide-container .slide-desc h5 {
    font-size: 18px;
  }
}

@media (max-width: 768px) {
  .product__list .slide-container .slide-desc h5 {
    font-size: 15px;
  }
}

.product__list .slide-container .slide-desc p {
  font-size: 20px;
  color: #fff;
}

@media (max-width: 992px) {
  .product__list .slide-container .slide-desc p {
    font-size: 16px;
  }
}

@media (max-width: 768px) {
  .product__list .slide-container .slide-desc p {
    font-size: 12px;
  }
}

.product__list .slide-container:hover .slide-desc {
  opacity: 0;
}

.product__list .slide-btn {
  border: 3px solid #11d111;
  border-radius: 5px;
  width: 150px;
  height: 150px;
  cursor: pointer;
}

.product__list .slide-btn img {
  height: 100%;
  width: 100%;
  position: relative;
}

.product__list .slide-btn:focus {
  border: 3px solid #eba119;
  outline: #eba119;
}

.certificate__block {
  padding-top: 15px;
}

.certificate__img {
  display: block;
  padding: 0 0 15px 0;
}

.news-page__list {
  display: flex;
  flex-direction: column;
}

.news-page__item {
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.news-page__item img {
  max-height: 400px;
  width: 100%;
  margin-bottom: 20px;
  object-fit: cover;
}

.news-page__item h3 {
  font-size: 28px;
}

.news-page__item h3 a {
  color: #000;
  transition: .5s;
}

.news-page__item h3 a:hover {
  color: #eba119;
}

.news-page__item p {
  text-align: justify;
}

.invest__info a:hover {
  transition: .5s;
  color: #eba119;
}

.contacts__info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (max-width: 768px) {
  .contacts__info iframe {
    width: 280px;
    height: 280px;
  }
}

.contacts h3 {
  font-size: 24px;
  padding-top: 20px;
}

.contacts h4 {
  font-size: 24px;
  padding-top: 20px;
}

.contacts td {
  padding: 5px 10px;
  border-bottom: 1px solid #000;
  border-color: #eba119;
}

@media (max-width: 768px) {
  .contacts td {
    font-size: 12px;
    padding: 5px;
  }
}

.ask .form {
  margin-top: 10px;
  max-width: 800px;
  width: 100%;
}

.ask .form__inp {
  display: flex;
}

.ask .form__name {
  max-width: 800px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ask .form__contacts {
  max-width: 800px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (max-width: 1140px) {
  .ask .form__contacts {
    margin-left: 5px;
  }
}

.ask .form__text {
  max-width: 800px;
  width: 100%;
}

.ask .form__input {
  max-width: 350px;
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
}

.ask .form button {
  display: block;
  margin: auto;
  padding: 10px;
  font-size: 16px;
  background: #eba119;
  outline: none;
  border: none;
  transition: .5s;
  color: #fff;
}

.ask .form button:hover {
  background: #11d111;
}
