.nav {
    &__info {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 30px 0;
        @include media($xs) {
            flex-direction: column;
            justify-content: center;
            text-align: center;
        }
    }
    &__logo {
        display: flex;
        align-items: center;
        div {
            max-width: 250px;
            width: 100%;
            margin-left: 10px;
            h1 {
                font-family: "Orbitron";
                font-family: 700px;
                text-transform: uppercase;
            }
            p {
                font-family: "Play";
                font-family: 400;
                text-transform: uppercase;
                font-size: 14px;
            }
        }
    }
    &__catalog {
        @include media($xs) {
            margin-top: 30px;
            display: inline-block;
        }
        a {
            font-family: "Play";
            font-size: 20px;
            text-transform: uppercase;
            color: $white;
            transition: .5s;
            &:hover {
                background: $orangeLinear;
            }
            @include media($sm) {
                font-size: 16px;
            }
        }
        &-pdf {
            background: $greenLinear;
            padding: 25px 40px 25px 45px;
            @include media($sm) {
                padding: 20px 30px 20px 35px;
            }
            @include media($xs) {
                padding: 10px 30px 10px 35px;
            }
        }
        &-doc {
            padding: 25px 20px 25px 20px;
            background: $salat;
            @include media($sm) {
                padding: 20px 20px 20px 20px;
            }
            @include media($xs) {
                padding: 10px 20px 10px 20px;
            }
        }
    }
    &__bar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: $greenLinear;
        position: relative;
        box-shadow: 0px 5px 23px -5px #000000;
        @include media($md) {
            justify-content: center;
        }
        @include media($sm) {
            justify-content: space-between;
        }
    }
    &__menu {
        position: relative;
        .burger {
            height: 25px;
            width: 30px;
            cursor: pointer;
            margin-left: 10px;
            position: relative;
            background: none;
            outline: none;
            border: none;
            display: none;
            z-index: 100;
            @include media($sm) {
                display: block;
            }
            &__line {
                display: block;
                width: 100%;
                height: 3px;
                background: $white;
                position: absolute;
                left: 0;
                transition: .3s all linear;
                z-index: 0;
                &_first {
                    top: 0;
                }
                &_second,
                &_third {
                    top: 50%;
                    transform: translateY(-50%);
                }
                &_fourth {
                    bottom: 0;
                }
            }
            &_active .burger__line_first,
            &_active .burger__line_fourth {
                opacity: 0;
            }
            &_active .burger__line_second {
                transform: translateY(-50%) rotate(45deg);
            }
            &_active .burger__line_third {
                transform: translateY(-50%) rotate(-45deg);
            }
        }
        .menu {
            display: flex;
            z-index: 1000;
            @include media($sm) {
                flex-direction: column;
                position: absolute;
                background: $bgGreen;
                top: 36px;
                border-top: 2px solid $orange;
                transform: scaleY(0);
                transform-origin: 50% 0 0;
                transition: .5s;
                opacity: 0;
                min-width: 250px;
                width: 100%;
                &.active {
                    transform: scaleY(1);
                    opacity: 1;
                }
            }
            @include media($xs) {
                top: 31px;
            }
            li.menu__item {
                position: relative;
                transition: 0.5s;
                a {
                    display: inline-block;
                    padding: 20px;
                    color: $white;
                    transition: .5s;
                    @include media($md) {
                        padding: 15px;
                    }
                    @include media($sm) {
                        width: 100%;
                    }
                    &:hover {
                        background: $greenOpacity;
                        color: $black;
                    }
                }
                .sub {
                    position: absolute;
                    background: $subMenu;
                    min-width: 250px;
                    width: 100%;
                    border-top: 2px solid $orange;
                    display: none;
                    z-index: 100;
                    @include media($sm) {
                        position: unset;
                    }
                    li {
                        a {
                            width: 100%;
                            z-index: 9999;
                            transition: .5s;
                            transform: translateX(0);
                            @include media($sm) {
                                padding-left: 30px;
                                position: relative;
                                &::before {
                                    content: "\f324";
                                    font-weight: 300;
                                    padding-right: 5px;
                                    color: $orange;
                                }
                            }
                        }
                    }
                }
                &:hover {
                    .sub {
                        transform: scaleY(1);
                        display: block;
                        opacity: 1;
                        @include media($sm) {
                            height: 100%;
                        }
                    }
                }
                /* .sub.active {
                    transform: scaleY(1);
                    opacity: 1;
                    @include media($sm) {
                        height: 100%;
                    }
                } */
            }
            li:nth-of-type(2)>.menu__link::after {
                content: "\f078";
                font-weight: 300;
                margin-left: 5px;
            }
            li:nth-of-type(3)>.menu__link::after {
                content: "\f078";
                font-weight: 300;
                margin-left: 5px;
            }
            li:nth-of-type(5)>.menu__link::after {
                content: "\f078";
                font-weight: 300;
                margin-left: 5px;
            }
        }
    }
    &__search {
        @include media($md) {
            display: none;
        }
        @include media($sm) {
            display: block;
        }
        input {
            padding: 21px 10px;
            border: none;
            outline: none;
            font-size: 15px;
            background: $greenOpacity;
            max-width: 150px;
            width: 100%;
            @include media($sm) {
                padding: 15px 8px;
            }
            @include media($xs) {
                max-width: 100px;
                padding: 10px 8px;
            }
        }
        button {
            padding: 21px 15px;
            border: none;
            outline: none;
            font-size: 15px;
            background: $green;
            color: $white;
            transition: .3s;
            @include media($sm) {
                padding: 15px 15px;
            }
            @include media($xs) {
                padding: 10px;
            }
            &:hover {
                background: $orange;
            }
        }
    }
    &__secondary {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include media($sm) {
            justify-content: center;
        }
        ul {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            li {
                a {
                    padding: 20px;
                    display: inline-block;
                    font-size: 12px;
                    color: grey;
                    transition: .5s;
                    position: relative;
                    z-index: 1;
                    &::before {
                        content: '';
                        display: block;
                        width: 5px;
                        height: 5px;
                        background: grey;
                        position: absolute;
                        left: 10px;
                        top: 50%;
                        transform: translate(-50%, -50%);
                        transition: .5s;
                        z-index: 1;
                    }
                    &:hover {
                        color: $orange;
                        &::before {
                            background: $orange;
                        }
                    }
                    @include media($xs) {
                        padding: 10px;
                        &::before {
                            left: 0;
                        }
                    }
                }
            }
        }
        .nav__search {
            display: none;
            @include media($md) {
                display: block;
                input {
                    border: 1px solid rgb(158, 149, 149);
                    padding: 10px;
                    border-radius: 10px;
                }
                button {
                    padding: 10px;
                    background: none;
                    color: rgb(158, 149, 149);
                    transition: 1s;
                    &:hover {
                        color: $orange;
                        transform: rotateY(360deg);
                    }
                }
            }
            @include media($sm) {
                display: none;
            }
        }
    }
}