.inner {
    &__doc {
        padding: 15px 0;
        &-desc {
            display: flex;
            margin-top: 10px;
            background: $grey;
            @include media($sm) {
                flex-direction: column;
                align-items: center;
            }
            img {
                max-width: 200px;
                width: 100%;
                max-height: 200px;
                height: 100%;
                @include media($sm) {
                    max-width: 100px;
                    padding-top: 10px;
                    border-radius: 10px;
                }
                @include media($xs) {
                    max-width: 50px;
                }
            }
        }

        &-text {
            padding: 15px;
            position: relative;

            a {
                position: absolute;
                display: block;
                text-transform: uppercase;
                background: $orange;
                color: $white;
                padding: 10px;
                right: 15px;
                bottom: 15px;
                transition: .3s;
                &:hover {
                    background: $green;
                }

                @include media($sm) {
                    position: unset;
                    width: max-content;
                    margin: 10px auto 0 auto;
                } 

                @include media ($xs) {
                    font-size: 12px;
                    padding: 5px;
                }
            }

            p {
                font-size: 16px;
                @include media ($xs) {
                    font-size: 12px;
                }
            }
        }
    }
}