.news {
    @include row(0);
    justify-content: space-between;
    padding: 50px 0;
    @include media($lg) {
        align-items: center;
        justify-content: center;
    }
    &__card {
        width: 270px;
        padding: 5px;
        @include media($lg){
            @include col-lg(6);
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        @include media($xs) {
            @include col-xs(12);
            text-align: center;
            padding: 10px;
        }
        &-opacity {
            width: 270px;
            height: 270px;
            position: relative;
            

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            .opacity {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: $darkGreen;
                display: flex;
                align-items: center;
                justify-content: center;
                opacity: 0;
                transition: .5s;
                .plus {
                    color: $white;
                    font-size: 36px;                    
                }
            }
            &:hover {
                .opacity {
                    opacity: 1;
                }
            }
        }  
        
        h6 {
            font-size: 20px;
            font-weight: 400;
            padding: 15px 0 5px;
            &::before {
                content: "\f054";
                padding-right: 10px;
                font-size: 16px;
                transform: translate(-50%, -50%);
                vertical-align: baseline;
            }
            a {
                color: $black;
            }
        }

        .news__link {
            font-size: 16px;
            color: grey;
            transition: .5s;
            &:hover {
                color: $orange;
                text-decoration: underline;
            }
        }
    }
    &__card:nth-of-type(1) {
        h5 {
            font-family: "Play";
            font-weight: 700;
            font-size: 24px;
            margin-bottom: 10px;
            max-width: 270px;
            width: 100%;
        }

        p {
            font-size: 18px;
            @include limit(5);
            margin-bottom: 10px;
            color: grey;
            max-width: 270px;
            width: 100%;
        }

        a {
            display: inline-block;
            color: $white;
            background: $bgGreen;
            padding: 20px;
            transition: .5s;
            
            &:hover {
                background: $orange;
            }
        }
    }
}