.standart {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px 0;
    @include media($xs) {
        flex-wrap: wrap;
        padding: 30px 0 15px 0;
    }

    &__card {
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 25%;
        cursor: pointer;
        padding-left: 15px;
        @include media($xs) {
            max-width: 50%;
            padding-right: 30px;
            padding-bottom: 15px;
            padding-left: 30px;
            text-align: center;
        }
                
        a {
            font-size: 56px;
            color: $orange;
            transition: .5s;
            @include media($md) {
                font-size: 42px;
            }
            @include media($sm) {
                font-size: 30px;
            }
        }

        &:hover {
            a {
                color: $green;
            }
        }
    }

    &__desc {
        padding-left: 15px;
        @include media($sm) {
            padding-left: 5px;
        }

        h4 {
            font-family: "Montserrat";
            font-size: 16px;
            color: $orange;
            @include media($md) {
                font-size: 12px;
            }
            @include media($sm) {
                font-size: 10px;
            }
        }

        p {
            font-family: "Montserrat";
            font-size: 14px;
            color: grey;
            padding-top: 5px;
            @include media($md) {
                font-size: 10px;
            }
            @include media($sm) {
                font-size: 8px;
            }
        }
    }

}