.offer {
    height: 450px;
    position: relative;
    @include media($xs) {
        height: 300px;
    }
    &__desc {
        position: absolute;
        z-index: 100;
        left: 0;
        top: 0;
    }
    &__block {
        display: flex;
        align-items: center;
        
        .offer__text {
            background: $darkGreen;
            display: flex;
            flex-direction: column;
            justify-content: center;
            height: 150px;
            padding: 0 20px 0 30px;
            opacity: 0;
            font-family: "Play";
            color: $white;
            @include media($xs) {
                height: 100px;
            }
            h4 {
                padding-bottom: 10px;
            }

            p {
                @include media($xs) {
                    @include limit(2);
                }
            }
        }

        .offer__name {
            display: flex;
            flex-direction: column;
            justify-content: center;
            height: 150px;
            background: $grey;
            padding: 0 10px 0 20px;
            font-family: "Play";
            position: relative;
            cursor: pointer;
            @include media($xs) {
                height: 100px;
            }
            a {
                padding-bottom: 10px;
                color: $black;
                font-weight: 700;
                @include media($xs) {
                    font-size: 16px;
                }
            }

            i {
                position: absolute;
                font-size: 24px;
                right: -16px;
                display: none;
                z-index: 100;
            }
            p {
                @include media($xs) {
                    @include limit(2);
                }
            }
            
        }
        &:hover {
            .offer__name {
                background: $orange;
                color: $white;
                a {
                    color: $white;
                }
            }
            i {
                display: block;
                color: $orange;
            }
            .offer__text {
                opacity: 1;
            }
        }
    }
    img {
        position: absolute;
        object-fit: cover;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
    }
}