$Fonts: ("Montserrat", "Regular", 400),
        ("Montserrat","Bold", 700),
        ("Comfortaa","Bold", 700),
        ("Comfortaa","Regular", 400),
        ("fontawesome","duotone", 900),
        ("fontawesome","light", 300),
        ("fontawesome","brands", 400),
        ("fontawesome","regular", 400),
        ("fontawesome","solid", 700),
        ("Roboto","Medium", 500),
        ("Roboto","Regular", 400),
        ("Orbitron","Regular", 400),
        ("Orbitron","Bold", 700),
        ("Play","Regular", 400),
        ("Play","Bold", 700);

@each $family, $name, $weight in $Fonts {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}-#{$name}.ttf);
        font-weight: $weight;
    }
}

$white: #fff;
