.work { 
    
    fieldset {
        border: 5px solid $orange;
        margin: 0 auto;
        margin-top: 30px;
        border-radius: 5px;
        padding: 25px 10px 25px 15px;
        display: flex;
        justify-content: space-between;
        max-width: 500px;
        width: 100%;

        legend {
            text-transform: uppercase;
            font-weight: 700;
            padding: 5px;
        }

        table {
            width: 100%;
            font-weight: 700;
            @include media($sm) {
            font-size: 10px;
            }
        }
        

        p {
            font-weight: 700;
            font-size: 18px;

            span {
                color: $orange;
            }
        }
    }
    &__day {
        margin-right: 10px;
    }
}