.delivery {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 50px 0;
    background: $grey;

    &__factory {
        text-align: center;
        max-width: 30%;
        width: 100%;
    }

    &__truck {
        max-width: 40%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        .dot {
            width: 10px;
            height: 10px;
            background: $orange;
            border-radius: 50%;
            position: relative;
        }
        .before::after {
            content: '';
            position: absolute;
            width: 90px;
            height: 2px;
            display: block;
            top: 50%;
            transform: translate(-50%, -50%);
            right: -130px;
            background: $orange;
        }
        p {
            font-size: 120px;
            color: $orange;
            @include media($sm) {
                font-size: 90px;
            }
            @include media($xs) {
                font-size: 60px;
            } 
        }
        .after::before {
            content: '';
            position: absolute;
            width: 90px;
            height: 2px;
            display: block;
            top: 50%;
            transform: translate(-50%, -50%);
            left: -40px;
            background: $orange;
        }
    }

    &__home {
        text-align: center;
        max-width: 30%;
        width: 100%;
    }

    &__icon {
        font-size: 86px;
        color: $green;
        @include media($sm) {
            font-size: 55px;
        }
        @include media($xs) {
            font-size: 30px;
        }
    }

    &__desc {
        font-size: 24px;
        font-weight: 700;
        @include media($sm) {
            font-size: 16px;
        }
        @include media($xs) {
            font-size: 12px;
        }
    }
}