footer {
    background: $green;
}
.footer {    
    display: flex;
    padding: 20px;
    color: $white;
    justify-content: space-between;
    @include media($sm) {
        flex-wrap: wrap;
        justify-content: space-around;
    }

    &__title {
        font-family: "Play";
        font-size: 18px;
        margin-bottom: 20px;
        width: max-content;
        &::after {
            content: '';
            width: 100%;
            height: 2px;
            display: block;
            background: $orange;
        }
    }

    &__partners {
        display: flex;
        flex-direction: column;
        align-items: center;
        @include col-xs(12);
        
        .rost {
            height: 25px;
            margin-bottom: 20px;
            @include media($md) {
                height: 20px;
            }
        }
        .kuhn {
            height: 80px;
            @include media($md) {
                height: 60px;
            }
        }
    }

    &__nav {
        
        
        @include media($xs) {
            display: flex;
            text-align: center;
            margin-top: 30px;
            padding: 10px 0;
            flex-direction: column;
            align-items: center;
        }
    }

    &__link {
        color: $white;
        display: inline-block;
        padding: 5px 0;
        transition: .5s;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            display: block;
            background: $orange;
            height: 1px;
            width: 0;            
            bottom: 0;
            left: 50%;
            transition: .5s;
        }
        &:hover {
            color: $orange;
            &::after {
                left: 0;
                width: 100%;
            }
        }
    }

    &__contacts {
        max-width: 300px;
        width: 100%;
        
        
        @include media($md) {
            max-width: 250px;
        }
        @include media($sm) {
            display: flex;
            padding-top: 30px;
            max-width: max-content;
            text-align: center;
            flex-direction: column;
            align-items: center;
        }

        a {
            display: block;
            color: $white;
            padding-bottom: 5px;
            transition: .5s;
            &:hover {
                color: $orange;
            }
        }
    }

    &__adres {
        font-family: "Play";
        font-size: 16px;  
        padding-bottom: 10px;      
    }

    &__rights {
        font-size: 12px;
        margin-top: 5px;
    }
}


