.about {
    grid-area: content;
    display: flex;
    .content {
        display: flex;
        flex-direction: column;
        padding-right: 10px;
        @include media($xs) {
            align-items: center;
        }
        img {
            min-width: 350px;
            width: 100%;
            min-height: 200px;
            height: 100%;
            object-fit: cover;
            margin: auto;
            @include media($xs) {
                min-width: 150px;
                min-height: 100px;
            }
        }
        h1 {
            font-size: 32px;
            font-weight: 700;
            text-transform: uppercase;
            position: relative;
            width: max-content;
            margin: 10px 0;
            @include media($sm) {
                font-size: 24px;
            }
            &::after {
                content: '';
                width: 100%;
                height: 3px;
                background: $orange;
                display: block;
                position: absolute;
                bottom: 0;
                left: 0;
            }
        }
        p {
            font-size: 18px;
            font-weight: 400;
            text-align: justify;
            @include media($sm) {
                font-size: 16px;
            }
        }
    }
}