.header {
    &__hot {
        display: flex;
        background: $grey;
        align-items: center;

        a {
            text-transform: uppercase;
            background: $bgGreen;
            color: $white;
            padding: 15px 10px;
            transition: .5s;
            font-size: 14px;
            min-width: max-content;

            &:hover {
                background: $orange;
            }
        }

        p {
            @include limit(1);
            color: grey;
            font-size: 14px;
            padding: 0 8px;
        }
    }

    .slideshow {
        position: relative;
        z-index: 1;
        height: 500px;
        width: 100%;        

        .slider-track {
            transition: all 1s cubic-bezier(0.7, 0, 0.3, 1);
        }

        .item {
            height: 500px;
            position: relative;
            z-index: 1;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                transition: all 1s cubic-bezier(0.7, 0, 0.3, 1);
                transform: scale(1.2);
                position: absolute;
            }

            &.slick-active img {
                transform: scale(1);
            }

            .slider__info {
                max-width: 250px;
                width: 100%;
                margin-top: 50px;
                position: absolute;
                z-index: 999;
                h2 {
                    font-family: "Play";
                    font-weight: 700;
                    background: $darkGreen;
                    width: max-content;
                    padding: 10px;
                    font-size: 28px;
                    color: $white;
                }
    
                p {
                    font-family: "Play";
                    background: $darkGreen;
                    width: max-content;
                    padding: 10px;
                    color: $white;
                }
            } 
            .slider__detail {
                width: 100%;
                max-width: 300px;
                padding-top: 50px;
                position: absolute;
                right: 0;
                height: 100%;
                background: $darkGreen;
                @include media($sm) {
                    max-width: 100%;
                    max-height: 190px;
                    bottom: 0;
                    padding-top: 0;
                    padding-bottom: 15px;
                    display: flex;    
                    align-items: center; 
                    flex-direction: column;           
                }
    
                .icon {
                    font-size: 66px;
                    text-align: center;
                    @include media($sm) {
                        font-size: 26px;
                    }
                }
    
                h3 {
                    font-size: 24px;
                    text-align: center;
                    font-family: "Play";
                    font-weight: 700;
                    color: $white;
                    @include media($sm) {
                        font-size: 16px;
                    }
                }
    
                p {
                    padding: 10px 15px 20px 15px;
                    font-family: "Play";
                    font-size: 14px;
                    color: $white;
                    @include media($sm) {
                        padding: 5px;
                        text-align: center;
                    }
                }
    
                a {
                    background: $orange;
                    color: $white;
                    text-transform: uppercase;
                    padding: 10px 15px;
                    transition: .5s;
                    display: block;
                    margin: auto;
                    width: max-content;
                    outline: none;
                    @include media($sm) {
                       margin-top: 10px;
                    }
                    &:hover {
                        background: $yellow;
                        color: $black;
                    }
                }
            }
        }
    }

